<template>
  <div>
    <v-card class="home__top__right">
      <v-form class="home__form d-flex align-center flex-column" rel="form">
        <v-card-title class="home__form__title">Регистрация</v-card-title>
        <div class="home__form__name">
            <v-text-field
              solo
              color="purple accent-4"
              class="home__input"
              v-model.trim="user.firstname"
              placeholder="Введите свое имя"
            ></v-text-field>
          <v-text-field
            solo
            color="purple accent-4"
            class="home__input"
            v-model.trim="user.lastname"
            placeholder="Введите свою фамилию"
          ></v-text-field>
        </div>
        <v-text-field
          solo
          class="home__input__email"
          color="purple accent-4"
          v-model.trim="user.email"
          placeholder="Введите свой email"
          :class="{invalid: 
            ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
        ></v-text-field>
        <div class="d-flex flex-row">
          <v-icon class="home__input__icon" size="40">@</v-icon>
          <v-text-field
            solo
            class="home__input__id"
            color="purple accent-4"
            v-model.trim="user.id"
            placeholder="Придумайте логин (вы не сможете его изменить)"
          ></v-text-field>
        </div>
        <div class="home__input__show d-flex flex-row">
          <v-text-field
            solo
            v-if="showPassword"
            type="text"
            class="home__input__password"
            v-model.trim="user.password"
            color="purple accent-4"
            placeholder="Введите свой пароль"
          ></v-text-field>
          <v-text-field
            solo
            v-else
            type="password"
            class="home__input__password"
            v-model.trim="user.password"
            color="purple accent-4"
            placeholder="Введите свой пароль"
          ></v-text-field>
          <v-switch
            color="#232946"
            v-model="showPassword"
            label="Показать пароль"
          ></v-switch>
        </div>
        <div class="home__input__err" v-for="(err, index) in errors" :key="index">
          <h2>{{ err }}</h2>
        </div>
        <v-btn class="home__form__btn" color="#b8c1ec" @click="dataPush"
          >Зарегистрироваться</v-btn
        >
      </v-form>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import { required, minLength, email } from 'vuelidate/lib/validators'
import "../scss/registration.scss";
import "../scss/mediaReg.css";

export default {
  name: "Registration",
  data() {
    return {
      user: {
        firstname: "",
        lastname: "",
        email: "",
        password: "",
        id: "",
        date: moment().format("MMM Do YY")
      },
      showPassword: false,
      errors: []
    };
  },
  validations: {
    firstname: {
      required,
      minLength: minLength(4)
    },
    lastname: {
      required,
      minLength: minLength(3)
    },
    email: {
      required,
      email
    },
    password: {
      required,
      minLength: minLength(6)
    }
  },
  methods: {
    async dataPush() {
      try {
        let response = await axios.post('/api/auth/signup', this.user)
        let jwt = response.data[0]
        let token = response.data[1]
        let user = response.data[2]
        
        this.$store.commit("loadData", user)
        localStorage.setItem("user", JSON.stringify(user))
        localStorage.setItem(jwt, token)
        this.$router.push("/users/" + user.id)
      } catch(error) {
        if (error.message == "Request failed with status code 400" 
            && this.errors.includes("Такой id / почта уже используется") == false) {
          this.errors.push("Такой id / почта уже используется")
        }
        console.log(error.message)
      }
    }
  }
};
</script>