import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dataDB: "",
    isRuleError: "",
    isUser: ""
  },
  mutations: {
    loadData(state, data) {
      state.dataDB = data
    },
    noRules(state, err) {
      state.isRuleError = err
    },
    isUserTrue(state, data) {
      state.isUser = data
    }
  },
  actions: {},
  modules: {}
});
