<template>
  <div>
    <v-card class="home__top__right">
      <v-form class="home__form d-flex align-center flex-column" rel="form">
        <v-card-title class="home__form__title">Вход</v-card-title>
        <div class="home__input__show d-flex align-center flex-column">
          <v-text-field
            class="home__input__email"
            color="purple accent-4"
            v-model="user.email"
            placeholder="Введите свой email"
            solo
          ></v-text-field>
          <v-text-field
            type="password"
            class="home__input__password"
            v-model="user.password"
            color="purple accent-4"
            placeholder="Введите свой пароль"
            solo
          ></v-text-field>
        </div>
        <div v-for="(err, index) in errors" :key="index">
          <h2 style="color: #FF5252">{{ err }}</h2>
        </div>
        <v-btn class="home__form__btn" color="#b8c1ec" @click="loginSend">
          Авторизоваться
        </v-btn>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
import "../scss/mediaReg.css";

export default {
  name: "Login",
  data() {
    return {
      user: {
        email: "",
        password: "",
      },
      errors: []
    };
  },
  methods: {
    async loginSend() {
      try {
        let res = await axios.post("/api/auth/login", this.user)
        this.user.email = ""
        this.user.password = ""

        let type = res.data[0]
        let token = res.data[1]
        let user = res.data[2]

        localStorage.setItem(type, token)
        localStorage.setItem("user", JSON.stringify(user))
        this.$router.push({name: "Account", params: {id: user.id}})

      } catch (err) {
        if (err.message == "Request failed with status code 404" && 
        this.errors.includes("Такого пользователя не существует") == false) 
          this.errors.push("Такого пользователя не существует")
        if (err.message == "Request failed with status code 400" && 
        this.errors.includes("Вы ввели не верный пароль") == false)
          this.errors.push("Вы ввели не верный пароль")
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.home {
  &__top {
    &__right {
      width: 500px;
      min-height: 300px;
    }
  }
  &__form {
    padding: 20px;
    &__title {
      font-size: 32px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  &__input {
    &__email {
      width: 400px;
    }
    &__password {
      width: 400px;
      margin-left: 10px;
    }
  }
}
</style>
